
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
computed: mapGetters([
'userInfromationObj'
]),
components: {
XHeader,
Group,
Cell,
Loading,
Alert,
Toast
},
data() {
return {
userInfo: '',
showLoding: false,
showHead: false,
headImageUrl: '',
errorShow: false,
errorContent: '',
successShow: false,
successContent: '',
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.initData();
},
methods: {
initData() {
this.showLoding = true;
let selectUser = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"sm_ui_ID": this.userInfo.sm_ui_ID
};
this.$store.dispatch('initUserInfromation', selectUser)
.then(() => {
this.showLoding = false;
})
},
update() {
this.$router.push({name: 'UpdatePersonalData'})
},

replaceHeadImage() {
this.uploaNode()
this.showHead = true;
},

uploadToOSS(file) {
return new Promise((relove,reject)=>{
var fd = new FormData();
fd.append("fileToUpload", file);
var xhr = new XMLHttpRequest();
xhr.open("POST", "http://hly.1000da.com.cn/OSSFile/PostToOSS");
xhr.send(fd);
xhr.onreadystatechange = function () {
if (xhr.readyState == 4 && xhr.status == 200) {
if (xhr.responseText) {
var data = xhr.responseText
relove(JSON.parse(data))
}
}else{

}
}
})
},

uploaNode() {
this.headImageUrl = '';
setTimeout(() => {
if (this.$refs.upload) {
this.$refs.upload.addEventListener('change', data => {
for (var i = 0; i < this.$refs.upload.files.length; i++) {
this.uploadToOSS(this.$refs.upload.files[i])
.then(data => {
if (data) {
this.headImageUrl = '';
this.headImageUrl = data.data;
} else {
this.$notify({
message: '图片地址不存在!',
type: 'error'
});
}
})
}
})
};
}, 30)
},

replaceImage() {
this.userInfromationObj.sm_ui_HeadImage = this.headImageUrl;

let updateUser = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"data": this.userInfromationObj
};
this.$store.dispatch('updatePersonalData', updateUser)
.then(suc => {
this.successShow = true;
this.successContent = suc;
this.initData();
}, err => {
this.errorShow = true;
this.errorContent = err;
})
}
},
}



