
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
computed: mapGetters([
'exensionDetailsList'
]),
components: {
XHeader,
Loading
},
data() {
return {
rows: 0,
userInfo: '',
id: '',
showLoding: false
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.id = this.userInfo.sm_ui_ID
},
methods: {
initData(id, rows) {
this.showLoding = true;
let getShareInfo = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": id ? id : '',//操作员编码
"operateUserName": "",
"pcName": "",
"sm_ii_UserID": id ? id : '',//推广员编号
"page": "1",
"rows": rows ? rows : 10
};
return this.$store.dispatch('initExensionDetails', getShareInfo)
},


refresh(done) {
this.initData(this.id, 10)
.then(() => {
this.showLoding = false;
done(true)
})

},

infinite(done) {
this.rows += 10;
this.initData(this.id, this.rows)
.then(() => {
this.showLoding = false;
done(true)
})

}
},
mounted() {
},

}



