
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
computed: mapGetters([
'getRedEnvelopesList'
]),
components: {
XHeader,
Loading
},
data() {
return {
id: '',
site: 1,
showLoding: false,
rows: 0,
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.id = this.userInfo.sm_ui_ID
},
methods: {


initData(id, rows, state) {
this.showLoding = true;
let getShare = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": id ? id : '',//操作员编码
"operateUserName": "",
"pcName": "",
"state": 1,
"page": "1",
"rows": rows ? rows : 10
};
return this.$store.dispatch('getRedEnvelopesDetails', getShare)
},


refresh(done) {
this.initData(this.id, 10)
.then(() => {
this.showLoding = false;
done(true)
})

},

infinite(done) {
this.rows += 10;
this.initData(this.id, this.rows)
.then(() => {
this.showLoding = false;
done(true)
})

}
},
mounted() {
}
}



