
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
computed: mapGetters([
'orderDataList'
]),
components: {
XHeader,
Actionsheet,
XButton,
Toast,
Loading
},
data() {
return {
menus: {
0: '旅行社',
1: '车票',
2: '门票',
3: '美食',
4: '酒店',
5: '租车',
'': '全部订单',
},
showMenus: false,
userInfo: '',
rows: 0,
id: '',
orderType: '全部订单',
errorShow: false,
errorContent: '',
successShow: false,
successContent: '',
showLoding: false,
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
},
mounted() {
this.top = 1
this.bottom = 20
},
methods: {
initData(id, rows) {
this.showLoding = true;
let getUserOrderDetail = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"huiuserid": this.userInfo.sm_ui_ID,
"orderType": id ? id : '',
"page": "1",
"rows": rows ? rows : 5,
"isDelete": 0
};
return this.$store.dispatch('getOrderData', getUserOrderDetail)

},

changeOrderType(menuKey, menuValue) {
this.id = menuKey;
this.orderType = menuValue;
this.initData(this.id)
.then(()=>{
this.showLoding = false;
})
},

deleteOrder(id) {
let deleteOrder = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"orderID": id ? id : ''
};
this.$store.dispatch('deleteOrder', deleteOrder)
.then(suc => {
this.successShow = true;
this.successContent = suc;
this.initData()
.then(() => {
this.showLoding = false;
})
}, err => {
this.errorShow = true;
this.errorContent = err;
})
},

refresh(done) {
this.initData(this.id,5)
.then(() => {
this.showLoding = false;
done(true)
})

},

infinite(done) {
this.rows += 5;
this.initData(this.id, this.rows)
.then(() => {
this.showLoding = false;
done(true)
})

}
},
}



